import React from 'react';
import { Container, Grid, Button } from '@mui/material';
import { Link } from 'react-router-dom'; 
import Header from '../Header';
import Footer1 from '../Footer1';

const Dashboard = () => {

  return (
    <div>
<Header/>
      <Container style={{ maxWidth: '100em',minHeight:'15rem', marginTop: '50px', backgroundColor: '#423B47', borderRadius: '20px', paddingLeft: '100px', paddingRight: '100px',boxShadow:'0px 10px 10px #ccc' }}>
        <div style={{ textAlign: 'center' }}>
          <h1 style={{ fontWeight: 'bolder', color:'aliceblue',paddingTop:'30px' }}>EMPLOYER DASHBOARD</h1>
        </div>
        <br /><br />
        <Grid container spacing={2} justifyContent="center">
          <Grid item xs={12} sm={3} md={3}>
            <Link to="/AddJobs"> 
              <Button variant="contained" fullWidth style={{backgroundColor:'#0D6EFD'}}>
                Add a Job
              </Button>
            </Link>
          </Grid>
          <Grid item xs={12} sm={3} md={3}>
            <Link to="/PostedJobs">
              <Button variant="contained" color="secondary" fullWidth>
                POSTED JOBS
              </Button>
            </Link>
          </Grid>
          <Grid item xs={12} sm={3} md={3}>
            <Link to="/ApprovedJobs">
              <Button variant="contained" fullWidth style={{ backgroundColor: 'GREEN' }}>
                APPROVED JOBS
              </Button>
            </Link>
          </Grid>
          <Grid item xs={12} sm={3} md={3}>
            <Link to="/Applicants">
              <Button variant="contained" fullWidth style={{ backgroundColor: '#00CCCD' }}>
                Applicants
              </Button>
            </Link>
          </Grid>
        </Grid>
      </Container>
      <br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br />
      <Footer1/>
    </div>
  );
};

export default Dashboard;


















