import axios from 'axios';
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import Header from './Header';
import login from "../assets/draw2.jpg"
import Footer1 from './Footer1';
const Login = () => {
  const navigate = useNavigate();
  const [user, setUser] = useState({});

  const inputHandler = (e) => {
    setUser({
      ...user,
      [e.target.name]: e.target.value,
    });
  };

  const submitHandler = () => {
    axios
      .post("/api/login", user)
      .then((response) => {
        if (response.data.message === "Login successfully") {
          const token = response.data.token;
          const userId = response.data.data._id;
          const userRole = response.data.data.role;
          const phone = response.data.data.phone;
          const name = response.data.data.name;

          console.log(token);
          console.log(userId);
          console.log(name);
          console.log(phone)
          console.log(userRole);
          
          sessionStorage.setItem("userToken", token);
          sessionStorage.setItem("userId", userId);
          sessionStorage.setItem("userRole", userRole);
          sessionStorage.setItem("phone", phone);
          sessionStorage.setItem("name", name);

          alert(response.data.message);
          navigate('/');
        } else if (response.data.message === "login failed") {
          alert(response.data.message);
        }
        else if(response.data.message === "user is not found"){
          alert(response.data.message);
        }
      })
      .catch((err) => console.log(err));
  };

  return (
    <div>
      <Header />
      <div className="container ">
      
        <div className="container d-flex justify-content-center align-items-center vh-100 " >
          
          <div className="row border border-dark rounded shadow" style={{borderRadius:'10px', border:'20px solid #552878'}} >
          <h2 style={{textAlign:'center',fontWeight:'bold',fontSize:'40px'}}>Log In</h2>
            <div className="col-md-6 p-5" >
              <img src= {login} alt="Login "className="img-fluid rounded"/>
            </div>
            <div className="col-md-6 p-5" >
              <label htmlFor="" className="form-label">Username</label>
              <input type="text" className="form-control border-dark" name="username" onChange={inputHandler}/>
              <label htmlFor="" className="form-label">Password </label>
              <input type="password" className="form-control border-dark" name="password" onChange={inputHandler} />
              <button className="btn btn-success mt-3" onClick={submitHandler}>Log In </button>
              <div className="mt-3">
                <a href="/signup">New users click here</a>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Footer1/>
    </div>
  );
};

export default Login;





























