import React, { useState } from 'react';
import { Container, Grid, Button, TextField, MenuItem, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle } from '@mui/material';
import axios from 'axios';
import { Link, useNavigate } from 'react-router-dom';
import { Padding } from '@mui/icons-material';
import Header from '../Header';
import Footer1 from '../Footer1';
const AddJobs = () => {
  const[userToken,setUserToken] =useState(sessionStorage.getItem('userToken'));
  const[userID,setUserId] = useState(sessionStorage.getItem('userId'));
  const navigate=useNavigate();

  
  const [formData, setFormData] = useState({
    jobTitle: '',
    companyName: '',
    location: '',
    salary: '',
    jobType: '',
    qualifications: '',
    skills: '',
    jobDescription: '',
    experience: '',
    contactDetails: '',
    lastDate: '',
    approved: false,
  });
  let data={
    userId:userID,
    token:userToken,
    jobTitle: formData.jobTitle,
    companyName: formData.companyName,
    location: formData.location,
    salary: formData.salary,
    jobType: formData.jobType,
    qualifications: formData.qualifications,
    skills: formData.skills,
    jobDescription: formData.jobDescription,
    experience: formData.experience,
    contactDetails: formData.contactDetails,
    lastDate: formData.lastDate
  }
  


  const [openPopup, setOpenPopup] = useState(false);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevFormData) => ({
      ...prevFormData,
      [name]: value,
    }));
  };

  // const handleFileChange = (e) => {
  //   const file = e.target.files[0];
  //   setFormData((prevFormData) => ({
  //     ...prevFormData,
  //     resume: file,
  //   }));
  // };

  const handleSubmit = (e) => {
    e.preventDefault();

    for (const key in formData) {
      if (formData[key] === '' || formData[key] === null) {
        alert('Please fill in all fields before submitting.');
        return;
      }
    }

    axios.post('/api/jobs', data)
      .then((response) => {
        // console.log('Job posted successfully:', response.data);
        // setOpenPopup(true);
        if(response.data.message==="Job added successfully"){
          alert(response.data.message);
          navigate('/PostedJobs');
          
      }
     else if(response.data.message==="Unable to add job"){
        alert(response.data.message);
     }

        // Reset the form 
        // setFormData({
        //   jobTitle: '',
        //   companyName: '',
        //   location: '',
        //   salary: '',
        //   jobType: '',
        //   qualifications: '',
        //   skills: '',
        //   jobDescription: '',
        //   experience: '',
        //   contactDetails: '',
        //   lastDate: '',
        //   approved: false,
        // });
      })
      .catch(err=>console.log(err));
      // .catch((error) => {
      //   console.error('Error posting job:', error);
      // });
  };

  const handleClosePopup = () => {
    setOpenPopup(false);
  };


  const handleCancel = () => {
    setFormData({
      jobTitle: '',
      companyName: '',
      location: '',
      salary: '',
      jobType: 'Select',
      qualifications: '',
      skills: '',
      jobDescription: '',
      experience: '',
      contactDetails: '',
      lastDate: '',
      approved: false,
    });
  };

  return (

    <div>
      <Header/>
      <Container style={{ maxWidth: '100em',minHeight:'15rem', marginTop: '50px', backgroundColor: '#423B47', borderRadius: '20px', paddingLeft: '100px', paddingRight: '100px',boxShadow:'0px 10px 10px #ccc' }}>
        <div style={{ textAlign: 'center' }}>
          <h1 style={{ fontWeight: 'bolder', color:'aliceblue',paddingTop:'30px' }}>EMPLOYER DASHBOARD</h1>
        </div>
        <br /><br />
        <Grid container spacing={2} justifyContent="center">
          <Grid item xs={12} sm={3} md={3}>
            <Link to="/AddJobs"> 
              <Button variant="contained" fullWidth style={{backgroundColor:'#0D6EFD'}}>
                Add a Job
              </Button>
            </Link>
          </Grid>
          <Grid item xs={12} sm={3} md={3}>
            <Link to="/PostedJobs">
              <Button variant="contained" color="secondary" fullWidth>
                POSTED JOBS
              </Button>
            </Link>
          </Grid>
          <Grid item xs={12} sm={3} md={3}>
            <Link to="/ApprovedJobs">
              <Button variant="contained" fullWidth style={{ backgroundColor: 'GREEN' }}>
                APPROVED JOBS
              </Button>
            </Link>
          </Grid>
          <Grid item xs={12} sm={3} md={3}>
            <Link to="/Applicants">
              <Button variant="contained" fullWidth style={{ backgroundColor: '#00CCCD' }}>
                Applicants
              </Button>
            </Link>
          </Grid>
        </Grid>
      </Container>

        
        <Container  style={{backgroundColor:'#552878',marginBottom:'30px',borderRadius:'50px',maxWidth:'76%'}}>
        <h2 style={{textAlign:'center',fontWeight:'bolder',marginTop:'40px',backgroundColor:'',color:'aliceblue',}}>POST A JOB </h2>
        </Container>
        
        <Container style={{ maxWidth: '100rem', marginTop: '50px', backgroundColor: 'aliceblue', borderRadius: '20px', paddingLeft: '100px', paddingRight: '100px', minHeight: '45rem'}}> 
        <form onSubmit={handleSubmit}>
        <Grid container spacing={2} style={{ marginTop: '30px' }}>
          <Grid item xs={12} md={6}>
            <TextField
              label="Job Title"
              name="jobTitle"
              value={formData.jobTitle}
              onChange={handleInputChange}
              fullWidth
              required
            />
          </Grid>

          <Grid item xs={12} md={6}>
            <TextField
              label="Company Name"
              name="companyName"
              value={formData.companyName}
              onChange={handleInputChange}
              fullWidth
              required
            />
          </Grid>

          <Grid item xs={12} md={6}>
            <TextField
              label="Location"
              name="location"
              value={formData.location}
              onChange={handleInputChange}
              fullWidth
              required
            />
          </Grid>

          <Grid item xs={12} md={6}>
            <TextField
              label="Salary"
              name="salary"
              value={formData.salary}
              onChange={handleInputChange}
              fullWidth
              required
            />
          </Grid>

          <Grid item xs={12} md={6}>
            <TextField
              select
              label="Job Type"
              name="jobType"
              value={formData.jobType}
              onChange={handleInputChange}
              fullWidth
              required
            >
              <MenuItem value="hybrid">Hybrid</MenuItem>
              <MenuItem value="remote">Remote</MenuItem>
              <MenuItem value="onsite">Onsite</MenuItem>
            </TextField>
          </Grid>
          <Grid item xs={12} md={6}>
            <TextField
              label="Qualifications"
              name="qualifications"
              value={formData.qualifications}
              onChange={handleInputChange}
              fullWidth
              required
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <TextField
              label="Skills"
              name="skills"
              value={formData.skills}
              onChange={handleInputChange}
              fullWidth
              required
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <TextField
              label="Experience"
              name="experience"
              value={formData.experience}
              onChange={handleInputChange}
              fullWidth
              required
            />
          </Grid>




          <Grid item xs={12} md={6}>
            <TextField
              label="Job Description"
              name="jobDescription"
              value={formData.jobDescription}
              onChange={handleInputChange}
              multiline
              rows={4}
              fullWidth
              required
            />
          </Grid>

          <Grid item xs={12} md={4}>
            <TextField
              label="Contact Details"
              name="contactDetails"
              value={formData.contactDetails}
              onChange={handleInputChange}
              fullWidth
              required
            />
          </Grid>

          <Grid item xs={12} md={2} style={{ alignContent: 'space-around' }}>
            <TextField
              type="date" 
              label="Last Date"
              name="lastDate"
              value={formData.lastDate}
              onChange={handleInputChange}
              fullWidth
              required
              InputLabelProps={{ shrink: true }}
            />
          </Grid>

          {/* <Grid item xs={12} md={12}>
            <input type="file" onChange={handleFileChange} />
          </Grid> */}

          <Grid item xs={4} md={6}>
            <Button variant="contained" onClick={handleCancel} style={{ marginTop: '5px', backgroundColor: 'black' }}>
              Cancel
            </Button>
          </Grid>

          <Grid item xs={8} md={6}>
            <Button type="submit" variant="contained" fullWidth color="primary" style={{ marginTop: '5px', backgroundColor: 'green' }} >
              Submit Job Post
            </Button>
          </Grid>
        </Grid>


      </form>
      <Dialog open={openPopup} onClose={handleClosePopup} maxWidth="sm">
        <DialogTitle>Job Posted Successfully!</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Your job has been posted successfully. It is now waiting for review.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClosePopup} color="primary" autoFocus>
            OK
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
    <br /><br /><br />
    <Footer1/>
    </div>
    
  );
};

export default AddJobs;