// import logo from './logo.svg';
import './App.css';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import Dashboard from './components/employer/Dashboard';
import PostedJobs from './components/employer/PostedJobs';
import AddJobs from './components/employer/AddJobs';
import Home from './components/Home';
import Signup from './components/Signup';
import Login from './components/Login';
import AdminJobs from './components/admin/AdminJobs';
import AdminReview from './components/admin/AdminReview';
import AdminEmployer from './components/admin/AdminEmployer';
import AdminUsers from './components/admin/AdminUsers';
import ApprovedJobs from './components/employer/ApprovedJobs';
import AdminApplicants from './components/admin/AdminApplicants';
import Applicants from './components/employer/Applicants';
import ViewUserProfile from './components/user/ViewUserProfile';
import Main from './components/user/Main';
import UserProfileCreation from './components/user/UserProfileCreation';
import AdminDashboard from './components/admin/AdminDashboard';
import AdminJobPosting from './components/admin/AdminJobPosting';
import Aboutus from './components/Aboutus';

function App() {
  
  return (
  
    <BrowserRouter>
    
    <Routes>
    
    <Route path='/' element={<Home/>}/>
    <Route path='/dashboard' element={<Dashboard/>}/>
      <Route path='login' element={<Login/>}/>
      <Route path='/signup' element={<Signup/>}/>
      <Route path='/aboutus' element={<Aboutus/>}/>
      <Route path='/postedjobs' element={<PostedJobs/>}/> 
      <Route path='/addjobs' element={<AddJobs/>}/> 
      <Route path='/approvedjobs' element={<ApprovedJobs/>}/>
      <Route path='/applicants' element={<Applicants/>}/>
      <Route path='/adminjobs' element={<AdminJobs/>}/>
      <Route path='/adminreview' element={<AdminReview/>}/>
      <Route path='/adminemployer' element={<AdminEmployer/>}/>
      <Route path='/adminusers' element={<AdminUsers/>}/>
      <Route  path='/adminapplicants' element={<AdminApplicants/>}/>
      <Route  path='/admindashboard' element={<AdminDashboard/>}/>
      <Route path='/userprofiles' element={<Main child={<ViewUserProfile/>}/>} />
      <Route path='/createprofile' element={<Main child={<UserProfileCreation method="post" 
          data={{name:"",email:"",phone:"",qualification:"",course:"",batch:"",status:"",company:""}} />}/>}/>
    <Route path='/adminjobpost' element={<AdminJobPosting/>}/>

    </Routes>
    </BrowserRouter>
    
  );

}


export default App;
