import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom'
import Footer1 from '../Footer1';

const UserProfileCreation = (props) => {
    const [redirect, setRedirect] = useState(false);

    const[post,setPost]=useState(props.data);
    console.log("method",props.method);
    console.log("data",props.data);
    const [selectedJob, setSelectedJob] = useState(null);
    const[userToken,setUserToken] =useState(sessionStorage.getItem('userToken'));
    const[userID,setUserId] = useState(sessionStorage.getItem('userId'));

    const navigate=useNavigate();

    const inputHandler=(e)=>{
            const {name,value} = e.target;
            setPost({
             ...post,[name]:value
            })
    }
    let inputs={
        userId:userID,
        token:userToken,
        name:post.name,
        email:post.email,
        phone:post.phone,
        qualification:post.qualification,
        course:post.course,
        batch:post.batch,
        status:post.status,
        company:post.company
    }

    
    
    const submitHandler=()=>{

        console.log('button is working',post);
        if(props.method==="post"){
        axios.post('/api/createprofile',inputs)
        .then((response)=>{
            if(response.data.message==="Profile created succesfully"){
                alert(response.data.message);
                navigate('/userprofiles');
                
            }
           else{
            alert(response.data.message);
           }
        })
        .catch(err=>console.log(err));
    }   
    if(props.method==="put"){
        // console.log("add clicked",post);
        axios.put('/api/updateprofile/'+post._id,post)
        .then((response)=>{
            if(response.data.message==="Updated Successfully "){
                alert(response.data.message);
                window.location.reload(false);
            }
            else{
                alert('Not Updated');
                window.location.reload(false);
                
            }
        })
    }
        

    }

  return (
    <div>
        <div className="container">
                <div className="row  align-items-center vh-70 p-5">
                <h2 style={{textAlign:'center', fontWeight:'bold', fontSize:'40px'}}>PROFILE CREATION</h2>
                
                    <div className="col col-12 col-sm-12 col-md-12 col-lg-12 border border-dark pt-5 " style={{borderRadius:'20px',marginTop:'65px'}}>
                        {/* <h3 className="container d-flex justify-content-center align-items-center">Profile creation</h3> */}
                        <div className="row g-3 me-3 mx-3">
                            <div className="col-12 col-sm-6 col-md-6 col-lg-6">
                                <label htmlFor="" className="form-lable">Name</label>
                                <input type="text" className="form-control border-dark" name="name" value={post.name}  onChange={inputHandler}/>
                            </div>
                            <div className="col-12 col-sm-6 col-md-6 col-lg-6">
                                <label htmlFor="" className="form-label">Email</label>
                                <input type="text" className="form-control border-dark" name="email"  value={post.email}  onChange={inputHandler}/>
                            </div>
                            <div className="col-12 col-sm-6 col-md-6 col-lg-6">
                                <label htmlFor="" className="form-label">Phone Number</label>
                                <input type="number" className="form-control border-dark" name="phone"  value={post.phone} onChange={inputHandler}/>

                            </div>
                            <div className="col-12 col-sm-6 col-md-6 col-lg-6">
                                <label htmlFor="" className="form-label">Highest Qualification</label>
                                <input type="text" className="form-control border-dark" name="qualification" value={post.qualification} onChange={inputHandler}/>
                            </div>
                            <div className="col-12 col-sm-6 col-md-6 col-lg-6">
                                <label htmlFor="role" className="form-label border-dark">Course studied at ICTAK</label>
                                <div className="dropdown">
                                    <select id="role" className="form-control border-dark" name="course" value={post.course} onChange={inputHandler}>
                                        <option value="" disabled selected>Select Course</option>
                                        <option >FSD</option>
                                        <option >DSA</option>
                                        <option >ML-AI</option>
                                        <option >RPA</option>
                                        <option >ST</option>
                                        <option >CSA</option>
                                    </select>
                                    <div className="dropdown-arrow"></div>
                                </div>
                            </div>
                            <div className="col-12 col-sm-6 col-md-6 col-lg-6">
                                <label htmlFor="role" className="form-label border-dark">Batch Details</label>
                                <div className="dropdown">
                                    <select id="role" className="form-control border-dark" name="batch" value={post.batch} onChange={inputHandler}>
                                        <option value="" disabled selected>Select Course</option>
                                        <option >KKEM</option>
                                        <option >NORKKA</option>
                                        <option >KDISC</option>
                                    </select>
                                    <div className="dropdown-arrow"></div>
                                </div>
                            </div>
                            <div className="col-12 col-sm-6 col-md-6 col-lg-6">
                                <label htmlFor="role" className="form-label border-dark">Placement Status</label>
                                <div className="dropdown">
                                    <select id="role" className="form-control border-dark" name="status" value={post.status} onChange={inputHandler}>
                                        <option value="" disabled selected>Select Status</option>
                                        <option >Placed</option>
                                        <option >Job-Seeking</option>
                                    </select>
                                    <div className="dropdown-arrow"></div>
                                </div>
                            </div>
                            <div className="col-12 col-sm-6 col-md-6 col-lg-6">
                                <label htmlFor="" className="form-label">Company Name(If placed)</label>
                                <input type="text" className="form-control border-dark" name="company" value={post.company} onChange={inputHandler}/>
                            </div>
                            <div className="col-12 col-sm-6 col-md-6 col-lg-6 mb-3">
                                <button className="btn btn-success" onClick={submitHandler}>Submit</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <br /><br /><br />
            <Footer1/>
    </div>
  )
}

export default UserProfileCreation