import React, { useEffect, useState } from 'react';
import Header from '../Header';
import axios from 'axios';
import UserProfileCreation from './UserProfileCreation';
import Footer1 from '../Footer1';


const ViewUserProfile = () => {
    const [data, setData] = useState([]);
    const [update, setUpdate] = useState(false);
    const [singleValue, setSingleValue] = useState([]);
    const [userToken, setUserToken] = useState(sessionStorage.getItem('userToken'));
    const [userID, setUserId] = useState(sessionStorage.getItem('userId'));

    const fetechDataFromApi = () => {
        axios.get('/api/profiles/' + userToken).then(
            (response) => {
                let userArray = response.data;
                userArray = userArray.filter((e) => e.email === sessionStorage.getItem('email'));
                setData(response.data);
            }
        );
    };

    const updateProfile = (val) => {
        setUpdate(true);
        setSingleValue(val);
    };

    useEffect(() => {
        fetechDataFromApi();
    }, []);

    let finalJSX = (
        <div>
            <div className="container mt-2">
                <div className="row mt-5">
                    <h2 style={{textAlign:'center', fontWeight:'bold', fontSize:'40px'}}>PROFILE</h2>
                    <div className="col col-12 col-sm-12 col-md-12">
                        <div className="row g-3 mt-3 mb-3">
                            {data.map((value, index) => (
                                <div className="col col-12 col-sm-12 col-md-12 col-lg-12" key={index}>
                                    <div className="card mb-3 border border-light border border-dark-subtle  ">
                                        <div className="row g-0">
                                            <div className="col-md-8">
                                                <div className="card-body p-5 ">
                                                    <h5 className="card-title p-2 fs-3">Name: {value.name}</h5>
                                                    <p className="card-text p-2 fs-3">Email: {value.email}</p>
                                                    <p className="card-text p-2 fs-3">Phone: {value.phone}</p>
                                                    <p className="card-text p-2 fs-3">Qualification: {value.qualification}</p>
                                                    <p className="card-text p-2 fs-3">Course: {value.course}</p>
                                                    <p className="card-text p-2 fs-3">Batch: {value.batch}</p>
                                                    <p className="card-text p-2 fs-3">Status: {value.status}</p>
                                                    <p className="card-text p-2 fs-3">Company: {value.company}</p>
                                                    <button className="btn btn-success me-2 p-2 fs-3" onClick={() => updateProfile(value)}>
                                                        Update
                                                    </button>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>
                </div>
            </div>
            <Footer1 />
        </div>
    );

    if (update) finalJSX = <UserProfileCreation method="put" data={singleValue} />;

    return finalJSX;
};

export default ViewUserProfile;
