import React, { useState, useEffect } from 'react';
import { Link, useNavigate, useLocation } from 'react-router-dom';
import EmployerNavbar from './employer/EmployerNavbar';
import "../App.css";

const Header = () => {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [isAdmin, setIsAdmin] = useState(false);
  const [isAlumni, setIsAlumni] = useState(false);
  const [isEmployer, setIsEmployer] = useState(false);
  const userRole = sessionStorage.getItem('userRole');
  const location = useLocation();

  useEffect(() => {
    setIsLoggedIn(!!userRole);
    setIsAdmin(userRole === 'Admin');
    setIsAlumni(userRole === 'alumni');
    setIsEmployer(userRole === 'employer');
  }, [userRole]);

  const navigate = useNavigate();

  const handleLogout = () => {
    sessionStorage.removeItem('userRole');
    setIsLoggedIn(false);
    setIsAdmin(false);
    setIsAlumni(false);
    setIsEmployer(false);
    navigate('/');
  };
  
  const isActiveDashboard = ['/dashboard', '/AddJobs', '/PostedJobs', '/ApprovedJobs', '/Applicants'].includes(location.pathname);
  return (
    <div>
      <nav className="navbar navbar-expand-lg" style={{ backgroundColor: '#360275', paddingTop: '0px' }}>
        <div className="container-fluid">
          <a className="navbar-brand" href="/" style={{ color: 'white', fontWeight: 'bold', paddingTop: '5px' }}>
            ICTAK ALUMNI JOB PORTAL
          </a>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarNavAltMarkup"
            aria-controls="navbarNavAltMarkup"
            aria-expanded="false"
            aria-label="Toggle navigation"
            style={{ backgroundColor: '#07ab0a' }}
          >
            <span className="navbar-toggler-icon" style={{ backgroundColor: '#07ab0a' }}></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
            <div className="navbar-nav ms-auto">
              {!isLoggedIn ? (
                // Links and UI elements for non-logged in users
                <>
                  <Link className={`nav-link ${location.pathname === '/' ? 'active' : ''}`} to="/" style={{ color: 'white', paddingTop: '15px' }}> Home </Link>
                  <Link className={`nav-link ${location.pathname === '/login' ? 'active' : ''}`} to="/login" style={{ color: 'white', paddingTop: '15px' }}>
                    Login
                  </Link>
                  <Link className={`nav-link ${location.pathname === '/signup' ? 'active' : ''}`} to="/signup" style={{ color: 'white', paddingTop: '15px' }}>
                    Sign up
                  </Link>
                  {/* <Link className="nav-link" to="#" style={{ color: 'white', paddingTop: '15px' }}>
                    Contact us
                  </Link> */}
                  <Link className={`nav-link ${location.pathname === '/aboutus' ? 'active' : ''}`} to="/aboutus" style={{ color: 'white', paddingTop: '15px' }}>
                    About us
                  </Link>
                </>
              ) : (
                // Links and UI elements for logged in users
                <>
                  {isAlumni ? (
                    // Links and UI elements for alumni users
                    <>
                      <Link className={`nav-link ${location.pathname === '/' ? 'active' : ''}`} to="/" style={{ color: 'white', paddingTop: '20px' }}>
                        Home
                      </Link>
                      <Link className={`nav-link ${location.pathname === '/userprofiles' ? 'active' : ''}`} to="/userprofiles" style={{ color: 'white', paddingTop: '20px' }}>
                        My Profile
                      </Link>
                      <Link className={`nav-link ${location.pathname === '/createprofile' ? 'active' : ''}`} to="/createprofile" style={{ color: 'white', paddingTop: '20px' }}>
                        Create Profile
                      </Link>
                      <Link className={`nav-link ${location.pathname === '/aboutus' ? 'active' : ''}`} to="/aboutus" style={{ color: 'white', paddingTop: '20px' }}>
                       About us
                       </Link>
                      <button className="nav-link" onClick={handleLogout} style={{ color: 'white', paddingTop: '1px' }}>
                        Log out
                      </button>
                    </>
                  ) : (
                    <>
                      {isAdmin ? (
                        
                        <>
                          <Link className={`nav-link ${location.pathname === '/' ? 'active' : ''}`} to="/" style={{ color: 'white', paddingTop: '20px' }}>
                            Home
                          </Link>
                          <Link className={`nav-link ${location.pathname === '/adminusers' ? 'active' : ''}`} to="/adminusers" style={{ color: 'white', paddingTop: '20px' }}>
                            Alumni
                          </Link>
                          <Link className={`nav-link ${location.pathname === '/adminemployer' ? 'active' : ''}`} to="/adminemployer" style={{ color: 'white',paddingTop:'20px' }}>
                           Employer
                       </Link>
                       <Link className={`nav-link ${location.pathname === '/adminjobpost' ? 'active' : ''}`} to="/adminjobpost" style={{ color: 'white',paddingTop:'20px' }}>
                         Add Jobs
                       </Link>
                        <Link className={`nav-link ${location.pathname === '/adminjobs' ? 'active' : ''}`} to="/adminjobs" style={{ color: 'white',paddingTop:'20px' }}>
                          Jobs
                       </Link>
                        <Link className={`nav-link ${location.pathname === '/adminapplicants' ? 'active' : ''}`} to="/adminapplicants" style={{ color: 'white',paddingTop:'20px' }}>
                            Applicants
                         </Link>
                          <Link className={`nav-link ${location.pathname === '/adminreview' ? 'active' : ''}`} to="/adminreview" style={{ color: 'white',paddingTop:'20px' }}>
                           Job Review
                          </Link>
                          <Link className={`nav-link ${location.pathname === '/aboutus' ? 'active' : ''}`} to="/aboutus" style={{ color: 'white', paddingTop: '20px' }}>
                            About us
                          </Link>
                          <button className="nav-link" onClick={handleLogout} style={{ color: 'white', paddingTop: '0px' }}>
                            Logout
                          </button>
                        </>

                        
                      ) : (
                        // Links and UI elements for employer users
                        <>
                          {isEmployer && (
                            <>
                              <Link className={`nav-link ${location.pathname === '/' ? 'active' : ''}`} to="/" style={{ color: 'white', paddingTop: '24px' }}>
                                Home
                              </Link>
                              <Link className={`nav-link ${isActiveDashboard ? 'active' : ''}`} to="/dashboard" style={{ color: 'white', paddingTop: '24px' }}>
                                Dashboard
                              </Link>
                              <Link className={`nav-link ${location.pathname === '/aboutus' ? 'active' : ''}`} to="/aboutus" style={{ color: 'white', paddingTop: '24px' }}>
                              About us
                              </Link>
                              <button className="nav-link" onClick={handleLogout} style={{ color: 'white', paddingTop: '' }}>
                                Logout
                              </button>
                            </>
                          )}
                        </>
                      )}
                    </>
                  )}
                </>
              )}
            </div>
          </div>
        </div>
      </nav>
    </div>
  );
};

export default Header;














