import React, { useState, useEffect } from 'react';
import { Container, Grid, Button, TextField, Card, CardContent, MenuItem, Typography, Dialog, DialogTitle, DialogContent, DialogActions } from '@mui/material';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { toast, ToastContainer } from 'react-toastify';
import EmployerNavbar from '../employer/EmployerNavbar';
import Header from '../Header';
import { CircularProgress } from '@mui/material';
import Footer1 from '../Footer1';

const ApprovedJobs = () => {
  const [approvedJobs, setApprovedJobs] = useState([]);
  const [showUpdatePopup, setShowUpdatePopup] = useState(false);
  const [selectedJob, setSelectedJob] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [userToken, setUserToken] = useState(sessionStorage.getItem('userToken'));
  const [userID, setUserId] = useState(sessionStorage.getItem('userId'));
  const [updatedJobData, setUpdatedJobData] = useState({
    jobTitle: '',
    companyName: '',
    location: '',
    salary: '',
    jobType: '',
    qualifications: '',
    skills: '',
    jobDescription: '',
    experience: '',
    contactDetails: '',
    lastDate: '',

  });

  const fetchApprovedJobs = async () => {
    try {
      const response = await axios.get('/api/jobs1/' + userToken);
      const jobs = response.data;
      const approvedJobs = jobs.filter((job) => job.approved);
      setApprovedJobs(approvedJobs);
    } catch (error) {
      console.error('Error fetching approved jobs:', error);
    }finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    fetchApprovedJobs();
  }, []);

  const handleDelete = (jobId) => {
    axios
      .delete(`/api/jobs/${jobId}`)
      .then((response) => {
        console.log('Job deleted successfully:', response.data);
        fetchApprovedJobs();
        toast.success('Job deleted', {
          position: toast.POSITION.BOTTOM_RIGHT,
          
        });
      })
      .catch((error) => {
        console.error('Error deleting job:', error);
        toast.error('Error deleting job', {
          position: toast.POSITION.BOTTOM_RIGHT,
          
        });
      });
  };

  const handleUpdate = (job) => {
    setSelectedJob(job);
    setShowUpdatePopup(true);
    setUpdatedJobData({
      jobTitle: job.jobTitle,
      companyName: job.companyName,
      location: job.location,
      salary: job.salary,
      jobType: job.jobType,
      qualifications: job.qualifications,
      skills: job.skills,
      jobDescription: job.jobDescription,
      experience: job.experience,
      contactDetails: job.contactDetails,
      lastDate: job.lastDate,
    });
  };

  const handleCloseUpdatePopup = () => {
    setShowUpdatePopup(false);
    setSelectedJob(null);
    setUpdatedJobData({
      jobTitle: '',
      companyName: '',
      location: '',
      salary: '',
      jobType: '',
      qualifications: '',
      skills: '',
      jobDescription: '',
      experience: '',
      contactDetails: '',
      lastDate: '',
    });
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setUpdatedJobData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  const handleUpdateSubmit = () => {
    axios.put(`/api/jobs/${selectedJob._id}`, updatedJobData)
      .then((response) => {
        console.log('Job updated successfully:', response.data);
        handleCloseUpdatePopup();
        fetchApprovedJobs();
        toast.success('Job details updated', {
          position: toast.POSITION.BOTTOM_RIGHT,
        
        });
      })
      .catch((error) => {
        console.error('Error updating job:', error);
        toast.error('Error updating job', {
          position: toast.POSITION.BOTTOM_RIGHT,
        });
      });
  };

  return (
    <div>
      <Header />
      <ToastContainer/>
      <Container style={{ maxWidth: '100rem', marginTop: '50px', backgroundColor: '#423B47', borderRadius: '20px', paddingLeft: '100px', paddingRight: '100px', minHeight: '15rem' }}>
      <div style={{ textAlign: 'center' }}>
          <h1 style={{ fontWeight: 'bolder',color:'aliceblue',paddingTop:'30px' }}>EMPLOYER DASHBOARD</h1>
        </div>
        <br /><br />
        <Grid container spacing={2} justifyContent="center">
          <Grid item xs={12} sm={3} md={3}>
            <Link to="/AddJobs"> 
              <Button variant="contained" fullWidth style={{backgroundColor:'#0D6EFD'}}>
                Add a Job
              </Button>
            </Link>
          </Grid>
          <Grid item xs={12} sm={3} md={3}>
            <Link to="/PostedJobs">
              <Button variant="contained" color="secondary" fullWidth>
                POSTED JOBS
              </Button>
            </Link>
          </Grid>
          <Grid item xs={12} sm={3} md={3}>
            <Link to="/ApprovedJobs">
              <Button variant="contained" fullWidth style={{ backgroundColor: 'GREEN' }}>
                APPROVED JOBS
              </Button>
            </Link>
          </Grid>
          <Grid item xs={12} sm={3} md={3}>
            <Link to="/Applicants">
              <Button variant="contained" fullWidth style={{ backgroundColor: '#00CCCD' }}>
                Applicants
              </Button>
            </Link>
          </Grid>
        </Grid>
        </Container>


        <Container  style={{backgroundColor:'#552878',marginBottom:'30px',borderRadius:'50px',maxWidth:'76%'}}>
        <h2 style={{textAlign:'center',fontWeight:'bolder',marginTop:'40px',backgroundColor:'',color:'aliceblue',}}>APPROVED JOBS </h2>
        </Container>

        <Container style={{ maxWidth: '100rem', marginTop: '50px', backgroundColor: 'aliceblue', borderRadius: '20px', paddingLeft: '100px', paddingRight: '100px', minHeight: '25rem',marginBottom:'20px' }}>

{isLoading ? (
          <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', minHeight: '200px',backgroundColor:'' }}>
            <CircularProgress style={{color:"#360275"}} />
          </div>
        ) : (
        <Grid container spacing={2} style={{ marginTop: '30px' }}>
          {approvedJobs.map((job) => (
            <Grid item key={job._id} xs={12} sm={6} md={4}>
              <Card className='postedjobs'>
                <CardContent>
                  <Typography variant="h5" component="h2">
                    {job.jobTitle}
                  </Typography>
                  <Typography variant="body2" color="textSecondary">
                    {job.companyName}
                  </Typography>
                  <Typography variant="body2" color="textSecondary">
                    {job.location}
                  </Typography>
                </CardContent>
                {/* Buttons for delete and update */}
                <Button variant="contained" style={{ backgroundColor: '#b00404', float: 'left', marginLeft: '10px', marginBottom: '5px' }} onClick={() => handleDelete(job._id)}>Delete</Button>
                <Button variant="contained" style={{ float: 'right', marginRight: '10px', marginBottom: '5px' }} onClick={() => handleUpdate(job)}>Update</Button>
              </Card>
            </Grid>
          ))}
        </Grid>
)}
        <Dialog open={showUpdatePopup} onClose={handleCloseUpdatePopup}>
          <DialogTitle>Update Job Details</DialogTitle>
          <DialogContent>
            <TextField
              label="Job Title"
              name="jobTitle"
              value={updatedJobData.jobTitle}
              onChange={handleChange}
              fullWidth
              margin="normal"
            />
            <TextField
              label="Company Name"
              name="companyName"
              value={updatedJobData.companyName}
              onChange={handleChange}
              fullWidth
              margin="normal"
            />
            <TextField
              label="Location"
              name="location"
              value={updatedJobData.location}
              onChange={handleChange}
              fullWidth
              margin="normal"
            />
            <TextField
              label="Salary"
              name="salary"
              value={updatedJobData.salary}
              onChange={handleChange}
              fullWidth
              margin="normal"
            />
            <TextField
              select
              label="Job Type"
              name="jobType"
              value={updatedJobData.jobType}
              onChange={handleChange}
              fullWidth
              margin="normal"
            >
              <MenuItem value="hybrid">Hybrid</MenuItem>
              <MenuItem value="remote">Remote</MenuItem>
              <MenuItem value="onsite">Onsite</MenuItem>
            </TextField>

            <TextField
              label="Qualifications"
              name="qualifications"
              value={updatedJobData.qualifications}
              onChange={handleChange}
              fullWidth
              margin="normal"
            />
            <TextField
              label="Skills"
              name="skills"
              value={updatedJobData.skills}
              onChange={handleChange}
              fullWidth
              margin="normal"
            />
            <TextField
              label="Experience"
              name="experience"
              value={updatedJobData.experience}
              onChange={handleChange}
              fullWidth
              margin="normal"
            />
            <TextField
              label="Job Description"
              name="jobDescription"
              value={updatedJobData.jobDescription}
              onChange={handleChange}
              fullWidth
              margin="normal"
            />
            <TextField
              type='date'
              label="Last Date "
              name="lastDate"
              value={updatedJobData.lastDate}
              onChange={handleChange}
              fullWidth
              margin="normal"
              InputLabelProps={{ shrink: true }}
            />
          </DialogContent>
          <DialogActions>
            <Button variant="contained" onClick={handleCloseUpdatePopup} color="primary">Cancel</Button>
            <Button variant="contained" onClick={handleUpdateSubmit} color="primary">Update</Button>
          </DialogActions>
        </Dialog>
      </Container>
      <Footer1/>
    </div>
  );
};

export default ApprovedJobs;






















