import axios from 'axios';
import React, { useState } from 'react';
import { useNavigate } from "react-router-dom";
import Header from './Header';
import Footer1 from './Footer1';

const Signup = () => {

    const[inputs,setInputs]=useState({});
    const navigate= useNavigate();
    const inputHandler=(e)=>{
        console.log('onchange is working');
        setInputs({
            ...inputs,[e.target.name]:e.target.value
        });
        console.log(inputs);
    }
    const submitHandler=()=>{
        console.log('button is working',inputs);
        axios.post("/api/signup",inputs)
        .then((response)=>{
           console.log(response);
           if(response.data.message==="Sign up successfull"){
            alert(response.data.message);
            navigate('/login');
           }
           else if(response.data.message==="Unable to register"){
            alert(response.data.message);
        }

        })
        .catch(err=>console.log(err))
    }




    return (
        <div>
            <Header/>
            <div className="container ">
                <div className="row  align-items-center vh-50 p-5" style={{marginTop:'10em',marginBottom:'6.11em'}}>
                    <div className="col col-12 col-sm-12 col-md-12 col-lg-12pt-5 " style={{borderRadius:'10px', border:'20px solid #552878'}}>
                        <h3 className="container d-flex justify-content-center align-items-center" style={{fontWeight:'bold',fontSize:'40px'}}>Sign Up</h3>
                        <div className="row g-3 me-3 mx-3">
                            <div className="col-12 col-sm-6 col-md-6 col-lg-6">
                                <label htmlFor="" className="form-lable">Name</label>
                                <input type="text" className="form-control border-dark" name="name"  onChange={inputHandler}/>
                            </div>
                            <div className="col-12 col-sm-6 col-md-6 col-lg-6">
                                <label htmlFor="" className="form-label">Email</label>
                                <input type="text" className="form-control border-dark" name="email"  onChange={inputHandler}/>
                            </div>
                            <div className="col-12 col-sm-6 col-md-6 col-lg-6">
                                <label htmlFor="" className="form-label">Phone Number</label>
                                <input type="number" className="form-control border-dark" name="phone"  onChange={inputHandler}/>

                            </div>
                            <div className="col-12 col-sm-6 col-md-6 col-lg-6">
                                <label htmlFor="role" className="form-label border-dark">Role</label>
                                <div className="dropdown">
                                    <select id="role" className="form-control border-dark" name="role"  onChange={inputHandler}>
                                        <option value="" disabled selected>Select Role</option>
                                        <option value="alumni">Alumni</option>
                                        <option value="employer">Employer</option>
                                    </select>
                                    <div className="dropdown-arrow"></div>
                                </div>
                            </div>
                            <div className="col-12 col-sm-6 col-md-6 col-lg-6">
                                <label htmlFor="" className="form-label ">Username</label>
                                <input type="text" className="form-control border-dark" name="username" onChange={inputHandler}/>
                            </div>
                            <div className="col-12 col-sm-6 col-md-6 col-lg-6">
                                <label htmlFor="" className="form-label">Password</label>
                                <input type="text" className="form-control border-dark" name="password"  onChange={inputHandler}/>
                            </div>
                            <div className="col-12 col-sm-6 col-md-6 col-lg-6 mb-3">
                                <button className="btn btn-success" onClick={submitHandler}>Sign Up</button>
                            </div>
                        </div>







                    </div>
                </div>
            </div>
            <Footer1/>
        </div>
    )
}

export default Signup