import React, { useEffect, useState } from 'react';
import '../App.css'
import Header from './Header';
import axios from 'axios';
import { Button, Card, CardContent, Dialog, DialogActions, DialogContent, DialogTitle, TextField } from '@mui/material';
import image2 from "../assets/home.jpeg"
import { Container } from 'react-bootstrap';
import image1 from "../assets/Homepage.gif"
import '../App.css'
import { toast, ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Footer from './Footer';
import Footer1 from './Footer1';

const Home = () => {
  const [data, setData] = useState([]);
  const [showApplyDialog, setShowApplyDialog] = useState(false);
  const [selectedJob, setSelectedJob] = useState(null);
  const [resumeLink, setResumeLink] = useState('');
  const [isLoggedIn, setIsLoggedIn] = useState(false);

  useEffect(() => {
    // Check if the user is logged in
    const userRole = sessionStorage.getItem('userRole');
    setIsLoggedIn(!!userRole);

    // Fetch job data
    axios.get("/api/jobs")
      .then((response) => {
        console.log(response.data);
        // setData(response.data.filter(job => job.approved));
        const currentDate = new Date();
      setData(response.data.filter(job => job.approved && new Date(job.lastDate) >= currentDate));
      })
      .catch((error) => {
        console.error("Error fetching data:", error);
      });
  }, []);

  const handleApplyClick = (job) => {
    // Check if the user is logged in as an alumni
    const userRole = sessionStorage.getItem('userRole');
    if (userRole === 'alumni') {
      setSelectedJob(job);
      setShowApplyDialog(true);
    } else {
      alert('Please SignUp or Login as an Alumni to apply for the job.');
    }
  };

  const handleCloseApplyDialog = () => {
    setSelectedJob(null);
    setShowApplyDialog(false);
    setResumeLink('');
  };

  const handleApplySubmit = () => {
    // Get user's phone and name from sessionStorage
    const phone = sessionStorage.getItem('phone');
    const name = sessionStorage.getItem('name');

    const applicationData = {
      jobTitle: selectedJob.jobTitle,
      phone: phone,
      name: name,
      resumeLink: resumeLink,
      approved: false,
    };

    axios.post('/api/apply', applicationData)
      .then((response) => {
        console.log('Job application submitted successfully:', response.data);
        handleCloseApplyDialog();
        toast.success('Job application submitted', {
          position: toast.POSITION.BOTTOM_RIGHT,
        });
      })
      .catch((error) => {
        console.error('Error submitting job application:', error);
        toast.error('Error submitting job application', {
          position: toast.POSITION.BOTTOM_RIGHT,
        });
      });
  };

  return (
    <div>
      <Header />
    <ToastContainer/>

      <Container style={{marginTop:'100px',maxWidth:'100rem'}}>
        <div className="row mt-5">
          <div className="col-12" >
            {/* <h3>Welcome to ICTAK Alumni Portal</h3><br /><br />
            <br /><br /> */}
            <div className="row" style={{ width: '53%',marginLeft:'5px' }}>
              <div class="typing-animation" >
                <span>Welcome to ICTAK </span>
                <span class="typed-text ">Alumni Job Portal</span>
                {/* <span class="cursor"></span> */}
                </div>
              </div>

              <Container className='mt-5'style={{maxWidth:'100rem'}}>
                <img src={image1} className="img-fluid rounded"  alt="" />
              </Container>

            
            <br /><br /><br />

            <Container style={{textAlign:'center',backgroundColor:'#552878',color:'aliceblue' ,marginBottom:'30px',borderRadius:'50px',maxWidth:'97rem'}}>
              <h2>LATEST JOBS</h2>
            </Container>

            <Container style={{backgroundColor:'#552878',borderRadius:'10px',maxWidth:'97rem'}}>
            <div className="row g-3">
              {data.map((job) => (
                <div className="col-12 col-sm-6 col-md-4 p-3" key={job._id}>
                  <Card className='postedjobs ' style={{backgroundColor:'aliceblue'}}>
                    <CardContent>
                      <h5 className="card-title">{job.jobTitle}</h5>
                      <p className="card-text">{job.companyName}</p>
                      <p className="card-text">{job.location}</p>
                      <Button variant="contained" onClick={() => handleApplyClick(job)}>Apply</Button>
                    </CardContent>
                  </Card>
                </div>
              ))}
            </div>
            </Container>
            <br /><br />
            
          </div>
        </div>
      </Container>



      {/* Apply Dialog  */}

      <Dialog open={showApplyDialog} onClose={handleCloseApplyDialog}>
        <DialogTitle style={{color:'#360275',fontWeight:'bolder',textAlign:'center'}}>Apply to Job</DialogTitle>
        {selectedJob && (
          <DialogContent>
            <ul style={{ listStyleType: 'none', padding: 0 }}>
              <li>
                <strong>Job Title:</strong> {selectedJob.jobTitle}
              </li>
              <li>
                <strong>Company Name:</strong> {selectedJob.companyName}
              </li>
              <li>
                <strong>Salary:</strong> {selectedJob.salary}
              </li>
              <li>
                <strong>Job Type:</strong> {selectedJob.jobType}
              </li>
              <li>
                <strong>Qualifications:</strong> {selectedJob.qualifications}
              </li>
              <li>
                <strong>Job Description:</strong> {selectedJob.jobDescription}
              </li>
              <li>
                <strong>Experience:</strong> {selectedJob.experience}
              </li>
              <li>
                <strong>Contact Details:</strong> {selectedJob.contactDetails}
              </li>
              <li>
                <strong>Expiry Date:</strong> {selectedJob.lastDate}
              </li>
            </ul>
            <TextField
              label="Paste Resume/CV Link"
              value={resumeLink}
              onChange={(e) => setResumeLink(e.target.value)}
              fullWidth
              required
            />
          </DialogContent>
        )}
        <DialogActions>
          <Button variant="contained" onClick={handleCloseApplyDialog} style={{ backgroundColor: 'black' }}>
            Close
          </Button>
          <Button variant="contained" onClick={handleApplySubmit} color="success">
            Apply
          </Button>
        </DialogActions>
      </Dialog>
      <Footer1/>
    </div>
  );
};

export default Home;




















