import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Container,Grid, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, Button } from '@mui/material';
import { Link } from 'react-router-dom';
import { CircularProgress } from '@mui/material';
import Header from '../Header';
import EmployerNavbar from './EmployerNavbar';
import Footer1 from '../Footer1';

const Applicants = () => {
  const [applicants, setApplicants] = useState([]);
  const[userToken,setUserToken] =useState(sessionStorage.getItem('userToken'));
  const[userID,setUserId] = useState(sessionStorage.getItem('userId'));
  const [isLoading, setIsLoading] = useState(true);
  
  

  const fetchApplicants = async () => {
    try {
      const response = await axios.get('/api/apply/'+userToken);
      const applicantsData = response.data.filter((applicant) => applicant.approved);
      setApplicants(applicantsData);
    } catch (error) {
      console.error('Error fetching applicants data:', error);
    }finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    fetchApplicants();
  }, []);

  const handleResumeLinkClick = (resumeLink) => {
    if (resumeLink.startsWith('http') || resumeLink.startsWith('www')) {
      window.open(resumeLink, '_blank');
    } else {
      window.open(`http://${resumeLink}`, '_blank');
    }
  };

  return (
    <div>
      <Header/>
      <Container style={{ maxWidth: '100rem', marginTop: '50px', backgroundColor: '#423B47', borderRadius: '20px', paddingLeft: '100px', paddingRight: '100px', minHeight: '15rem' }}>
      <div style={{ textAlign: 'center' }}>
          <h1 style={{ fontWeight: 'bolder',color:'aliceblue',paddingTop:'30px' }}>EMPLOYER DASHBOARD</h1>
        </div>
        <br /><br />
        <Grid container spacing={2} justifyContent="center">
          <Grid item xs={12} sm={3} md={3}>
            <Link to="/AddJobs"> 
              <Button variant="contained" fullWidth style={{backgroundColor:'#0D6EFD'}}>
                Add a Job
              </Button>
            </Link>
          </Grid>
          <Grid item xs={12} sm={3} md={3}>
            <Link to="/PostedJobs">
              <Button variant="contained" color="secondary" fullWidth>
                POSTED JOBS
              </Button>
            </Link>
          </Grid>
          <Grid item xs={12} sm={3} md={3}>
            <Link to="/ApprovedJobs">
              <Button variant="contained" fullWidth style={{ backgroundColor: 'GREEN' }}>
                APPROVED JOBS
              </Button>
            </Link>
          </Grid>
          <Grid item xs={12} sm={3} md={3}>
            <Link to="/Applicants">
              <Button variant="contained" fullWidth style={{ backgroundColor: '#00CCCD' }}>
                Applicants
              </Button>
            </Link>
          </Grid>
        </Grid>
        </Container>


        <Container  style={{backgroundColor:'#552878',marginBottom:'30px',borderRadius:'50px',maxWidth:'76%'}}>
        <h2 style={{textAlign:'center',fontWeight:'bolder',marginTop:'40px',backgroundColor:'',color:'aliceblue',}}>APPLICANTS </h2>
        </Container>

        <Container>
        <TableContainer component={Paper} style={{boxShadow:'0px 10px 10px #ccc'}}>
        {isLoading ? (
          <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', minHeight: '200px',backgroundColor:'' }}>
            <CircularProgress style={{color:"#360275"}} />
          </div>
        ) : (
          <Table>
            <TableHead>
              <TableRow>
                <TableCell style={{backgroundColor:'#360275', color:'aliceblue', fontWeight:'bolder',fontSize:'16px'}}>SL No</TableCell>
                <TableCell style={{backgroundColor:'#360275', color:'aliceblue', fontWeight:'bolder',fontSize:'16px'}}>Job Title</TableCell>
                <TableCell style={{backgroundColor:'#360275', color:'aliceblue', fontWeight:'bolder',fontSize:'16px',textAlign:'center'}}>Applicant Name</TableCell>
                <TableCell style={{backgroundColor:'#360275', color:'aliceblue', fontWeight:'bolder',fontSize:'16px',textAlign:'center'}}>Phone Number</TableCell>
                <TableCell style={{backgroundColor:'#360275', color:'aliceblue', fontWeight:'bolder',fontSize:'16px'}}>Resume Link</TableCell>
              </TableRow>
            </TableHead>
            <TableBody style={{backgroundColor:'aliceblue'}}>
              {applicants.map((applicant, index) => (
                <TableRow key={applicant._id}>
                  <TableCell>{index + 1}</TableCell>
                  <TableCell >{applicant.jobTitle}</TableCell>
                  <TableCell style={{textAlign:'center'}}>{applicant.name}</TableCell>
                  <TableCell style={{textAlign:'center'}}>{applicant.phone}</TableCell>
                  <TableCell>
                    <Button
                      variant="text"
                      style={{ textTransform: 'none', textDecoration: 'underline', color: 'blue',textDecoration:'none' }}
                      onClick={() => handleResumeLinkClick(applicant.resumeLink)}
                    >
                      View Resume
                    </Button>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        )}
        </TableContainer>
        </Container>
        <Footer1/>
    </div>
  );
};

export default Applicants;
